import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { UserService } from '../../services/user.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {

  newUser: any = {
    fname: '',
    lname: '',
    username: '',
    email: '',
    mobile: '',
    password: '',
    type: 'learner'
  };

  constructor(
    private userService: UserService,
    private flashMessage: FlashMessagesService,
    private router: Router
  ) { }

  ngOnInit() {
  }

  resetForm(form: NgForm): void {
    var elements = document.getElementsByTagName('input');
    for(var i = 0; i < elements.length; i++)
      elements.item(i).blur();
    form.reset();

    this.newUser.fname = "";
    this.newUser.lname = "";
    this.newUser.username = "";
    this.newUser.email = "";
    this.newUser.mobile = '';
    this.newUser.password = "";
  }

  onFormSubmit(form: NgForm) {
    this.userService.addUser(this.newUser).subscribe(data => {
      if(data.success){
        //show success message
        if(data.msg){
          this.flashMessage.show(data.msg, {cssClass: 'alert-success', timeout: 1500});
        }else{
          this.flashMessage.show("New user added to the system", {cssClass: 'alert-success', timeout: 1500});
        }

        this.router.navigate(['/home']);
      }else{
        if(data.msg){
          this.flashMessage.show(data.msg, {cssClass: 'alert-danger', timeout: 1500});
        }else{
          this.flashMessage.show("You are not registered to the system", {cssClass: 'alert-danger', timeout: 1500});
        }

        //reset form
        this.resetForm(form);
      }
    });
  }

  onCancelClick(form: NgForm): void {
    this.router.navigate(['/home']);
  }
}



