import { Component, OnInit, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { LstreamService } from '../../services/lstream.service';
import { UserService } from '../../services/user.service';
import { InstructorService } from '../../services/instructor.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { DomSanitizer } from '@angular/platform-browser';
import { OnlineService } from '../../services/online.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-instructor',
  templateUrl: './instructor.component.html',
  styleUrls: ['./instructor.component.css']
})
export class InstructorComponent implements OnInit, OnDestroy{

  constructor(
    private lStream: LstreamService,
    private userService: UserService,
    private flashMessage: FlashMessagesService,
    private sanitizer: DomSanitizer,
    private instructorService: InstructorService,
    private OnlineService: OnlineService,
    private router: Router,
    private cdr: ChangeDetectorRef
  ) { }

  embedcode: String = "";
  online: boolean = false;
  videoSrc: String = "";
  user: any = {};
  instructor: any ={};

  message: string ="";
  chat: string = "Bot\nHello\n------------------------\n";

  onlineUsers = [];

  ngOnInit(): void {
    this.userService.validateToken();

    this.user = this.userService.loadUserData();

    this.lStream.getLStream(this.userService.loadUserData().username).subscribe(data => {
      if(data.success){
        this.online = true;
        var urls = data.stream.embedcode.split("/");
        this.embedcode = "https://www.youtube.com/embed/"+ urls[urls.length-1];
        console.log(this.embedcode);
        this.flashMessage.show("You are online now.", {cssClass: 'alert-success', timeout: 1500});
      }
    });

    this.instructorService.getInstructorProfile(this.user.username).subscribe(data => {
      if(data.success){
        this.instructor = data.ins;
      }else{
        this.flashMessage.show("Error in retrieving instructor's profile", {cssClass: "alert-danger", timeout: 1500});
      }
    });

    this.OnlineService.getSocket().disconnect();
    this.OnlineService.getSocket().connect();
    this.OnlineService.sendConnectMessage();

    this.OnlineService.getSocket().fromEvent<any>("receive").map(data => data.data).subscribe(data => {
      var message = JSON.parse(data);
      if(message.username === this.user.username)
        message.username = "You";
      var msg = message.username+"\n"+message.msg+"\n------------------------\n";
      this.chat = this.chat + msg;
    });

    this.OnlineService.getSocket().fromEvent<any>("broadcast").map(data => data.user).subscribe(data => {
      data = JSON.parse(data);
      var newArray = [];
      var j = 0;
      var d;
      var updated = false;
      for(var i = 0; i < this.onlineUsers.length; i++){
        d = this.onlineUsers[i];
        if(data.username === d.username)
          updated = true;
        if(data.username !== d.username)
          newArray.push(d);
      }
      if(!updated)
        newArray.push(data);
      this.onlineUsers = newArray;
    });

    this.OnlineService.getSocket().fromEvent<any>("remove").map(data => data.user).subscribe(data => {
      data = JSON.parse(data);
      var newArray = [];
      for(var i = 0; i < this.onlineUsers.length; i++){
        if(this.onlineUsers[i].id !== data.id)
          newArray.push(this.onlineUsers[i]);
      }
      this.onlineUsers = newArray;
    });
  }

  onLinkSubmit(): void {
      this.userService.validateToken();
      var user = this.userService.loadUserData();
      var stream = {
        username: user.username,
        code: this.embedcode,
        courseno: "cs001"
      }

      this.lStream.createLStream(stream).subscribe(data => {
        if(data.success){
          this.online = true;
          this.flashMessage.show("You are online now.", {cssClass: 'alert-success', timeout: 1500});
          this.router.navigate(['/instructor']);
        }else{
          console.log(data.msg);
          this.flashMessage.show("Error in starting live stream. Close the Hangout App now.", {cssClass: 'alert-danger', timeout: 1500});
        }
      });
  }

  onStopClick(): void {
    this.userService.validateToken();
    this.online = false;
    this.videoSrc = "";
    this.lStream.deleteLStream(this.userService.loadUserData().username).subscribe(data => {
      if(data.success)
        this.flashMessage.show("You are ofline now.", {cssClass: 'alert-success', timeout: 1500});
      else
        this.flashMessage.show("Error in stoping live stream. Close the Hangout App now.", {cssClass: 'alert-danger', timeout: 1500});
    });
  }

  ngOnDestroy(): void {
      this.OnlineService.getSocket().disconnect();
  }

  sendMessage(): void {
    this.OnlineService.sendMessage(this.message);
    this.message = "";
  }
}



