//Service to handle forgot password RequestOptions
//This service mainly works with http requests
import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Config } from '../app.config';
import { UserService } from './user.service';


import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';

@Injectable()

export class InstructorService {

	//REST api server address
	private server: string = Config.BACKEND_SERVER;

	constructor(
		private http: Http,
		private router: Router,
		private flashMessage: FlashMessagesService,
    private userService: UserService
	) { }

  addInstructor(instructor): Observable<any> {
    this.userService.validateToken();

		let headers = new Headers();
		headers.append('Content-Type', 'application/json');
		headers.append('Authorization', this.userService.loadToken());

		return this.http.post(this.server + 'instructor/add', JSON.stringify(instructor), {headers: headers}).map((res: Response) => res.json());
	}

  updateInstructorDetails(newInstructor): Observable<any> {
    this.userService.validateToken();

    let headers = new Headers();
		headers.append('Content-Type', 'application/json');
		headers.append('Authorization', this.userService.loadToken());

		return this.http.post(this.server + 'instructor/update', JSON.stringify(newInstructor), {headers: headers}).map((res: Response) => res.json());
  }

  getInstructorProfile(username):  Observable<any> {
		this.userService.validateToken();

		let headers = new Headers();
		headers.append('Content-Type', 'application/json');
		headers.append('Authorization', this.userService.loadToken());

		return this.http.get(this.server + 'instructor/profile', {headers: headers}).map((res: Response) => res.json());
	}
}



