import { Component, OnInit, AfterViewInit, ElementRef, ViewChild, OnDestroy } from '@angular/core';
import { LstreamService } from '../../services/lstream.service';
import { DomSanitizer } from '@angular/platform-browser';
import { OnlineService } from '../../services/online.service';
import { UserService } from '../../services/user.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-liveclass',
  templateUrl: './liveclass.component.html',
  styleUrls: ['./liveclass.component.css']
})

export class LiveclassComponent implements OnInit, OnDestroy {

  @ViewChild('awwboard') board: ElementRef;

  constructor(
    private lstream: LstreamService,
    private sanitizer: DomSanitizer,
    private OnlineService: OnlineService,
    private userService: UserService,
    private router: Router
  ) { }

  course_name: String = "Human Computer Interaction";

  stream: any = {};
  message: string ="";
  chat: string = "Bot\nHello\n------------------------\n";

  user: any = {};

  ngOnInit() {
    this.userService.validateToken();

    this.user = this.userService.loadUserData();

    this.lstream.getStreamByCourseno("cs001").subscribe(data => {
      if(data.success){
        this.stream = data.stream;
        var link = this.stream.embedcode.split("/");
        this.stream.embedcode = "https://youtube.com/embed/"+link[link.length-1];
      }
    });

    this.OnlineService.getSocket().disconnect();
    this.OnlineService.getSocket().connect();
    this.OnlineService.sendConnectMessage();

    this.OnlineService.getSocket().fromEvent<any>("receive").map(data => data.data).subscribe(data => {
      var message = JSON.parse(data);
      if(message.username === this.user.username)
        message.username = "You";
      var msg = message.username+"\n"+message.msg+"\n------------------------\n";
      this.chat = this.chat + msg;
    });


  }

  ngOnDestroy(): void {
      this.OnlineService.getSocket().disconnect();
  }

  sendMessage(): void {
    this.OnlineService.sendMessage(this.message);
    this.message = "";
  }

}



