import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { UserService } from '../../services/user.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {

  loggedIn: boolean;
  name: String = '';
  isAdmin: boolean;

  constructor(
    private userService: UserService,
    private flashMessage: FlashMessagesService,
    private router: Router
  ) { }

  ngOnInit() {
  }

  logIn(): void {
    this.loggedIn = true;
    var user = this.userService.loadUserData();
    this.name = user.firstname+" "+user.lastname;
    if(user.type === 'admin' || user.type === 'instructor')
      this.isAdmin = true;
    else
      this.isAdmin = false;
  }

  clickButton(name: String): void {
    if(name === 'login')
      this.router.navigate(['/login']);
    else if(name === 'signup')
      this.router.navigate(['/signup']);
  }

  onDashboardClick(): void {
    var type = this.userService.loadUserData().type;
    if(type === 'admin')
      this.router.navigate(['/admin/user']);
    else
      this.router.navigate(['/instructor']);
  }

  openCourse(): void {
    this.router.navigate(['/course']);
  }

  homeClick(): void {
    this.router.navigate(['/home']);
  }

  onLogoutClick(): void {
    this.userService.logout().subscribe(response => {
      if(response.success){
				this.userService.clearStorage();
        this.loggedIn = false;
        this.isAdmin = false;
        this.name = '';
				this.flashMessage.show(response.msg, {cssClass: 'alert-success', timeout: 1500});
        this.router.navigate(['/home']);
			}
		});
  }

}



