//Service to handle forgot password RequestOptions
//This service mainly works with http requests
import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Config } from '../app.config';

import { UserService } from './user.service';

import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';

@Injectable()

export class LstreamService {

	//REST api server address
	private server: string = Config.BACKEND_SERVER;

	constructor(
		private http: Http,
		private router: Router,
    private userService: UserService,
		private flashMessage: FlashMessagesService
	) { }

	//Function to send email using the backend rest api
  createLStream(stream): Observable<any> {
      this.userService.validateToken();

      let headers = new Headers();
      headers.append('Content-Type', 'application/json');
      headers.append('Authorization', this.userService.loadToken());
      return this.http.post(this.server + 'lstream/add', JSON.stringify(stream), {headers: headers}).map((res: Response) => res.json());
  }

  getLStream(username): Observable<any> {
    this.userService.validateToken();

    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    headers.append('Authorization', this.userService.loadToken());

		return this.http.get(this.server + 'lstream/getByInstructor', {headers: headers}).map((res: Response) => res.json());
  }

	getStreamByCourseno(courseno): Observable<any> {
		this.userService.validateToken();

		let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    headers.append('Authorization', this.userService.loadToken());

		let params: URLSearchParams = new URLSearchParams();
		params.set('courseno', courseno);

		return this.http.get(this.server + 'lstream/livestream', {headers: headers, search: params}).map((res: Response) => res.json());
	}

  getAllStreams(): Observable<any> {
    this.userService.validateToken();

    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    headers.append('Authorization', this.userService.loadToken());

    return this.http.get(this.server + 'lstream/all', {headers: headers}).map((res: Response) => res.json());
  }

  deleteLStream(username): Observable<any> {
    var reqBody = {
      username: username
    };

    this.userService.validateToken();

    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    headers.append('Authorization', this.userService.loadToken());

    return this.http.delete(this.server + 'lstream/delete', {headers: headers, body: JSON.stringify(reqBody)}).map((res: Response) => res.json());
  }

	//Function to verify the link clicked by user in the email
	verifyEmailLink(hash): Observable<any> {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post(this.server + 'forgetpass/sendEmail', JSON.stringify(hash), {headers: headers}).map((res: Response) => res.json());
  }

	//If link is verified then use this function to reset the password
  resetPassword(resetObject): Observable<any> {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post(this.server + 'forgetpass/resetPassword', JSON.stringify(resetObject), {headers: headers}).map((res: Response) => res.json());
  }
}



